<template>
 <div class="footer">
     <p>Quer adicionar uma calourada?</p>
     <router-link to="/add-event" class="add" >Adicionar</router-link>
 </div>
</template>

<script>
export default {
  name: 'FooterCalendar',
  data () {
    return {
      visible: this.activate
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.footer{
    background-color: #40034a;
    color: #b3b3b3;
    text-align: center;
    font-size: 14px;
    font-size: 1.4rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

.footer .add {
    background: #d27849;
    border: none;
    padding: 12px;
    color: #ffffff;
    text-decoration: none;
    font-size: 0.8em;
    border-radius: 5%;
}
</style>



