<template>
  <div id="calendar">
    <UIBlock :activate="uiblock" :class="{ visible: uiblock }"></UIBlock>
    <vue-event-calendar :events="demoEvents">
        <template slot-scope="props">
          <div v-for="event in props.showEvents" :key="event.id" class="event-item">
            <div class="title-date">
              <div class="item-date">
                <span>{{ eventDay(event.date) }}</span>
                <span>{{ eventMonth(event.date) }}</span>
              </div>
              <h2 class="item-title">{{event.title}}</h2>
            </div>
            <div class="desc-location">
              <div class="item-location">
                <a  :href="event.local" target="_blank">
                  <svg :class="{ disable: event.local === null}" id="map-icon" xmlns:dc="http://purl.org/dc/elements/1.1/" xmlns:cc="http://creativecommons.org/ns#" xmlns:rdf="http://www.w3.org/1999/02/22-rdf-syntax-ns#" xmlns:svg="http://www.w3.org/2000/svg" xmlns="http://www.w3.org/2000/svg" height="49.999992" width="37.499992"  version="1.1" viewBox="0 0 37.499992 49.999992"> <metadata id="metadata10"> <rdf:RDF> <cc:Work rdf:about=""><dc:format>image/svg+xml</dc:format><dc:type rdf:resource="http://purl.org/dc/dcmitype/StillImage" /><dc:title></dc:title></cc:Work></rdf:RDF></metadata><defs id="defs8" /> <path id="path2" d="M 16.823043,48.99119 C 2.6337884,28.42098 0,26.30985 0,18.74999 0,8.39462 8.3946269,0 18.749995,0 c 10.355369,0 18.749996,8.39462 18.749996,18.74999 0,7.55986 -2.633789,9.67099 -16.823043,30.2412 -0.931152,1.34512 -2.922851,1.34502 -3.853905,0 z m 1.926952,-22.4287 c 4.314745,0 7.812499,-3.49775 7.812499,-7.8125 0,-4.31474 -3.497754,-7.8125 -7.812499,-7.8125 -4.314745,0 -7.812498,3.49776 -7.812498,7.8125 0,4.31475 3.497753,7.8125 7.812498,7.8125 z" /></svg>
                </a>
              </div>
            </div>
          </div>
      </template>
    </vue-event-calendar>
    <FooterCalendar></FooterCalendar>
  </div>
</template>

<script>
import UIBlock from '@/components/UIBlock'
import FooterCalendar from '@/components/FooterCalendar'

export default {
  name: 'BodyCalendar',
  components: { UIBlock, FooterCalendar },
  data () {
    return {
      demoEvents: [],
      uiblock: false
    }
  },
  mounted () {
    this.$http.get('http://localhost:8000/api/list').then(response => {
      response.body.data.forEach(element => {
        element.local = element.local !== null
          ? 'https://www.google.com/maps/search/?api=1&query=' + encodeURI(element.local)
          : null
      })
      mesTexto(1)
      this.demoEvents = response.body.data
      this.uiblock = true
    }, response => {
      console.error(response)
    })
  },
  methods: {
    eventDay: function (date) {
      return date.substring(5, 10).split('/')[1]
    },
    eventMonth: function (date) {
      console.log(date)
      return mesTexto(date.substring(5, 10).split('/')[0])
    }
  }
}
function mesTexto (numMes) {
  switch (numMes) {
    case '01':
      return 'JAN'
    case '02':
      return 'FEV'
    case '03':
      return 'MAR'
    case '04':
      return 'ABR'
    case '05':
      return 'MAI'
    case '06':
      return 'JUN'
    case '07':
      return 'JUL'
    case '08':
      return 'AGO'
    case '09':
      return 'SET'
    case '10':
      return 'OUT'
    case '11':
      return 'NOV'
    case '12':
      return 'DEZ'
    default: return ''
  }
}

</script>

<style>
.__vev_calendar-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: flex-start;
  margin: 10px;
}

.__vev_calendar-wrapper .cal-wrapper .cal-header{
  background: #40034a !important
}

.__vev_calendar-wrapper .arrow-right.icon,.__vev_calendar-wrapper .arrow-left.icon {
  color: #fff;
}
.cal-wrapper{
  order: 0;
}

.events-wrapper{
  order: 1;
  background-color: #179548 !important;
}

.visible {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 1.5s, opacity 0.05s linear;
}

.event-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.title-date {
  display: flex;
  align-items: center;
}

.item-date{
  display: flex;
  flex-direction: column;
  color: #560562;
  font-weight: bold;
  margin-right: 20px;
  font-size: 1em;
}
.item-title {
  font-size: 1.2em;
  color:#d27849;
}

.events-wrapper .date {
  display: none;
}

#map-icon {
  fill: #560562;
  height: 35px;
}

#map-icon.disable{
  fill: #b9b9b9;
}
</style>



