<template>
 <div class="add">
     <h2>Nova calourada</h2>
     <div class="add-form">
        <div class="group">
            <input v-validate="'required'" v-model="event.name" type="text" name="nome">
            <span class="highlight"></span>
            <span class="bar"></span>
            <label>Nome*</label>
            <span>{{ errors.first('nome') }}</span>
        </div>
        <div class="group">
            <input v-validate="'required'" v-model="event.date" type="date" name="data">
            <span class="highlight"></span>
            <span class="bar"></span>
            <label>Data*</label>
            <span>{{ errors.first('data') }}</span>
        </div>
        <div class="group">
            <input v-validate="'required'" v-model="event.location"
              ref="autocomplete"
              class="search-location"
              type="text"
              name="local" />
            <span class="highlight"></span>
            <span class="bar"></span>
            <label>Local*</label>
            <span>{{ errors.first('local') }}</span>
        </div>
        <div class="group">
            <textarea v-model="event.description"></textarea>
            <span class="highlight"></span>
            <span class="bar"></span>
            <label>Descrição</label>
        </div>
        <div class="group">
          <vuerecaptcha
            ref="recaptcha"
            @verify="onVerify"
            @expired="onExpired"
            sitekey="6LflHVkUAAAAAOrEE-ye-_RdDv_fTOO8errSm8Xe"></vuerecaptcha>
        </div>
        <div class="group btn-group">
            <router-link to="/" class="btn btn-cancel" >Voltar</router-link>
            <a href="#/add-event" class="btn btn-submit" v-on:click="validateForm()">Cadastrar</a>
        </div>
    </div>
    <MyModal v-if="showModal" @close="showModal = false">
      <h3 slot="header">{{ modalMessage }}</h3>
    </MyModal>
 </div>
</template>

<script>
import vuerecaptcha from 'vue-recaptcha'
import MyModal from '@/components/MyModal'

export default {
  name: 'AddEvent',
  components: { vuerecaptcha, MyModal },
  data () {
    return {
      visible: this.activate,
      event: { date: new Date().toISOString().substring(0, 10) },
      isHuman: false,
      validator: null,
      showModal: false,
      modalMessage: '',
      hasError: false
    }
  },
  mounted () {
    /* global google */
    var defaultBounds = new google.maps.LatLngBounds(
      new google.maps.LatLng(-10.923443, -37.105612),
      new google.maps.LatLng(-10.923443, -37.105612))

    this.autocomplete = new google.maps.places.Autocomplete(
      (this.$refs.autocomplete),
      {bounds: defaultBounds,
        types: ['establishment']}
    )

    this.autocomplete.addListener('place_changed', () => {
      let place = this.autocomplete.getPlace()
      var location = place.name + ', ' + place.formatted_address
      this.event.location = location
    })
  },
  methods: {
    // submit form handler
    submit: function () {
      var data = {
        name: this.event.name,
        date: this.event.date + 'T00:00:00-07:00',
        location: this.event.location,
        description: this.event.description
      }
      this.$http.post('http://localhost:8000/api/store', data)
        .then(function (response) {
          if (response.errors.length === 0) {
            this.modalMessage = 'Cadastrado com sucesso!!'
          } else {
            this.modalMessage = response.errors[0]
            this.hasError = true
          }
          this.showModal = true
        })
        /* eslint handle-callback-err: "error" */
        .catch(function (error) {
          this.modalMessage = 'Algo inexperado aconteceu D:'
          this.hasError = true
          this.showModal = true
        })
    },
    validateForm: function () {
      this.$validator.validateAll().then((result) => {
        if (result && this.isHuman) {
          this.submit()
        }
      })
    },
    onVerify: function (response) {
      this.isHuman = true
    },
    onExpired: function () {
      this.isHuman = false
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/* form starting stylings ------------------------------- */
.add-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: #fff
}

.btn-group{
  display: flex;
}
.btn {
    border: none;
    padding: 12px;
    color: #fff;
    text-decoration: none;
    width: 100px;
    margin-left: 10px;
}

.btn-submit {
    background: #179548;
    font-weight: bold;

}

.btn-cancel {
    background: #d27849;
}

.group {
  position:relative;
  margin-bottom:45px;
}
input, textarea {
  background: #560562;
  font-size:18px;
  padding:10px 10px 10px 5px;
  display:block;
  width:300px;
  border:none;
  border-bottom:1px solid #757575;
  color: #fff;
}
input:focus, textarea:focus{ outline:none; }

/* LABEL ======================================= */
label {
  color:#999;
  font-size:18px;
  font-weight:normal;
  position:absolute;
  pointer-events:none;
  left:5px;
  top:10px;
  transition:0.2s ease all;
  -moz-transition:0.2s ease all;
  -webkit-transition:0.2s ease all;
}

/* active state */
input:focus ~ label, input:valid ~ label, textarea:focus ~ label, textarea:valid ~ label {
  top:-20px;
  font-size:14px;
  color: #d27849;
}

/* BOTTOM BARS ================================= */
.bar { position:relative; display:block; width:300px; }
.bar:before, .bar:after {
  content:'';
  height:2px;
  width:0;
  bottom:1px;
  position:absolute;
  background:#d27849;
  transition:0.2s ease all;
  -moz-transition:0.2s ease all;
  -webkit-transition:0.2s ease all;
}
.bar:before {
  left:50%;
}
.bar:after {
  right:50%;
}

/* active state */
input:focus ~ .bar:before, input:focus ~ .bar:after,
textarea:focus ~ .bar:before, textarea:focus ~ .bar:after  {
  width:50%;
}

/* HIGHLIGHTER ================================== */
.highlight {
  position:absolute;
  height:60%;
  width:100px;
  top:25%;
  left:0;
  pointer-events:none;
  opacity:0.5;
}

/* active state */
input:focus ~ .highlight,
textarea:focus ~ .highlight {
  -webkit-animation:inputHighlighter 0.3s ease;
  -moz-animation:inputHighlighter 0.3s ease;
  animation:inputHighlighter 0.3s ease;
}

/* ANIMATIONS ================ */
@-webkit-keyframes inputHighlighter {
  from { background:#d27849; }
  to { width:0; background:transparent; }
}
@-moz-keyframes inputHighlighter {
  from { background:#d27849; }
  to { width:0; background:transparent; }
}
@keyframes inputHighlighter {
  from { background:#d27849; }
  to { width:0; background:transparent; }
}

</style>



